.App {
  text-align: center;
}

.App-header {
  background-color: darkgray;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.button {
  width: 80%;
  height: 300px;
  border-radius: 50%;
  background: red;
  font-size: 60px;
  color: white;
  border: 0px;
  box-shadow: 0 5px #999;
}

.button:active {
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

.block {
  top: 10%;
  font-size: 30px;
  position: absolute;
  width: 100%;
}

.next {
  margin-top: 10px;
  width: 150px;
  height: 30px;
}


